import React, { useEffect, useState } from 'react';
import { Button, Form, Header, Segment, Statistic,Message,Confirm, FormGroup,Label } from 'semantic-ui-react';
import { API, showError, showSuccess } from '../../helpers';
import { renderQuota } from '../../helpers/render';

const Recharge = () => {
  const [redemptionCode, setRedemptionCode] = useState(100);
  const [topUpLink, setTopUpLink] = useState('');
  const [userQuota, setUserQuota] = useState(0);
  const [userPoint, setUserPoint] = useState(0);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [open,setOpen] = useState(false);

  const openConfirm = () =>  { 
    if(redemptionCode<100){
      showError('100')
      return;
    }
    if(redemptionCode>userPoint){
      showError(',')
      return;
    }
    if(redemptionCode>1000000){
      showError('100')
      return;
    }
    setOpen(true) 
  }
  const closeConfirm = () => {setOpen(false)}

  const recharge = async () => {
    if(redemptionCode<100){
      showError('100')
        return;
    }
    if(redemptionCode>userPoint){
      showError(',')
      return;
    }
    if(redemptionCode>1000000){
      showError('100')
      return;
    }
    setIsSubmitting(true);
    try {
      const res = await API.post('/api/user/recharge', {
        point: redemptionCode
      });
      const { success, message, data } = res.data;
      if (success) {
        showSuccess('');
        setUserQuota((quota) => {
          return quota + data;
        });
        setRedemptionCode(100);
      } else {
        showError(message);
      }
    } catch (err) {
      showError('');
    } finally {
      setIsSubmitting(false); 
      setOpen(false);
    }
  };

  const openTopUpLink = () => {
    if (!topUpLink) {
      showError('');
      return;
    }
    window.open(topUpLink, '_blank');
  };

  const getUserQuota = async ()=>{
    let res  = await API.get(`/api/user/point`);
    const {success, message, data} = res.data;
    if (success) {
      setUserQuota(data.quota);
      setUserPoint(data.point);
    } else {
      showError(message);
    }
  }

  useEffect(() => {
    let status = localStorage.getItem('status');
    if (status) {
      status = JSON.parse(status);
      if (status.top_up_link) {
        setTopUpLink(status.top_up_link);
      }
    }
    getUserQuota().then();
  }, []);

  return (
    <Segment>
      <Header as='h3'></Header>

      <Statistic.Group widths='three'>
            <Statistic>
              <Statistic.Value>{userPoint.toLocaleString()}</Statistic.Value>
              <Statistic.Label>
                <br/>
                <a href='#' onClick={openTopUpLink}> </a>
              </Statistic.Label>              
            </Statistic>
            <Statistic>
              <Statistic.Value>{userQuota.toLocaleString()}</Statistic.Value>
              <Statistic.Label>token</Statistic.Label>
            </Statistic>
            <Statistic>
              <Statistic.Value>{renderQuota(userQuota)}</Statistic.Value>
              <Statistic.Label></Statistic.Label>
            </Statistic>
      </Statistic.Group>

      <Segment basic>
        <Form>
          <FormGroup inline>
              <label></label>
              <Form.Input
                focus
                placeholder=''
                name='redemptionCode'
                value={redemptionCode}
                onChange={(e) => {
                  setRedemptionCode(e.target.value);
                }}
                input='number'
                minLength='3'
                maxLength='7'
              />
              <Button color='orange' onClick={openConfirm} disabled={isSubmitting}>
                  {isSubmitting ? '...' : 'Token'}
              </Button>
             
          </FormGroup>
          <Label>
                 Token{(redemptionCode*500).toLocaleString()}<br/>
              </Label>

              <Message info>
                  <Message.List>
                      <Message.Item>1:500100=50,000 token</Message.Item>
                      <Message.Item>1001,000,000</Message.Item>
                      <Message.Item>500,000 token1</Message.Item>
                      <Message.Item>token</Message.Item>
                  </Message.List>
              </Message>
        </Form>
      </Segment>

          <Confirm
            open={open}
            header=''
            content='token'
            onCancel={closeConfirm}
            onConfirm={recharge}
            cancelButton=''
            confirmButton=""
            size='mini'
            />
    </Segment>
  );
};

export default Recharge;